.dialog-content{
    word-wrap: break-word;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    -ms-flex-positive: 1;
        flex-grow: 1;
    color: #70777B;
    font-size: 14px;
}

.dialog-content-accent{
    color: #000;
}

.dialog-active .dialog-content,
.dialog-active .dialog-content-accent,
.dialog-active .dialog-content-draft{
    color: #fff;
}

.dialog-content-draft{
    color: #c05f5a;
}


