.document{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.document-tile{
    position: relative;
    cursor: pointer;
}

.document-progress{
    position: absolute;
    top: 0;
    left: 0;
    width: 42px;
    height: 42px;
    margin: 3px;
    -webkit-animation: spin 4s linear infinite;
            animation: spin 4s linear infinite;
}

.document-progress-circle {
    stroke-linecap: round;
    color: #90a8bc;
}

.document-content{
    -ms-flex-positive: 1;
        flex-grow: 1;
    margin-left: 10px;
}

.document-title{
    padding-top: 6px;
}

.document-name{
    color: #3a6d99;
    font-weight: 700;
    max-width: 110px;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.document-size{
    color: #999;
}

.document-action{

}

@-webkit-keyframes spin {
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


